.ibt-link-hover a::after {
  content: "";
  position: absolute;
  width: 100%;
  height: 2px;
  bottom: 0;
  left: 0;
  background-color: currentColor;
  transform: scaleX(0);
  transform-origin: bottom right;
  transition: transform 0.3s;
}
.ibt-link-hover a:hover::after {
  transform-origin: bottom left;
  transform: scaleX(1);
}

